@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap);
.ant-menu {
  margin-top: 7rem !important;
}

.ant-menu.ant-menu-dark {
  background: #222629 !important;
}

.ant-menu.ant-menu-dark .ant-menu-item-selected {
  background-color: #0f0f0f !important;
}

.ant-menu-item {
  height: 70px !important;
  padding-top: 16px !important;
  padding-left: 40px !important;
  padding-right: 40px !important;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}

.menu-title {
  font-size: 3rem;
  color: #fff;
  text-align: right;
  padding: 40px 40px 0px 40px;
  margin-bottom: 0;
}

.menu-text {
  font-size: 1.4rem;
  float: right;
}

.ant-menu-item .anticon {
  font-size: 1.4rem !important;
  margin-top: 10px;
}

.typewriter-container {
  margin-top: 50px;
}

.Typewriter {
  font-size: 1.4rem;
  float: right;
  color: rgba(255, 255, 255, 0.65);
  padding: 0 40px;
}

.ant-layout-sider {
  width: 378px !important;
  min-width: 378px !important;
  max-width: 378px !important;
  height: 100% !important;
  background: #222629 !important;
  z-index: 5;
}

#nav {
  height: 100%;
  position: absolute !important;
  top: 0;
  left: 0;
  transition: left 0.8s;
}

.hide-nav {
  left: -378px !important;
}

.menu-footer {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 0;
  background: inherit !important;
  color: #fff !important;
}

.menu-footer-copyright {
  opacity: 0.45;
}

.footer-icons {
  margin-bottom: 40px;
}

.footer-icon {
  font-size: 2.4rem;
  opacity: 0.65;
  color: inherit;
}

.stack-overflow-icon {
  width: 38px;
  -webkit-filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(146deg)
    brightness(100%) contrast(104%);
          filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(146deg)
    brightness(100%) contrast(104%);
}

.footer-icon:hover {
  opacity: 1;
  color: inherit;
}

.btn-nav {
  z-index: 10;
  position: absolute;
  top: 0;
  left: 378px;
  transition: left 0.8s;
}

.btn-nav-push {
  left: 0;
}

.ant-btn-icon-only.ant-btn-lg > * {
  font-size: 30px !important;
  margin-top: -2px !important;
}

.ant-btn,
.ant-btn:focus {
  background: #fff !important;
  color: #000 !important;
  border: none !important;
}

.ant-btn:hover {
  background: #000 !important;
  color: #fff !important;
  border: none !important;
}

.fun-mode {
  position: absolute;
  bottom: 180px;
  left: 148px;
  z-index: 5;
  transition: left 0.8s;
}

.fun-mode .text-white {
  font-family: 'Reenie Beanie', cursive;
  opacity: 0.75;
  font-size: 22px;
  line-height: 1.1;
}

.ant-switch {
  margin-top: 5px !important;
}

.ant-switch-checked {
  background: #fff !important;
}

.ant-switch-checked .ant-switch-inner {
  color: #000 !important;
}

.ant-switch-checked .ant-switch-handle::before {
  background: #000 !important;
}

@media (max-width: 767px) {
  .ant-layout-sider {
    width: 220px !important;
    min-width: 220px !important;
    max-width: 220px !important;
  }

  .ant-menu {
    margin-top: 5rem !important;
  }

  .menu-title {
    font-size: 2.7rem;
    padding: 20px 20px 0px 20px;
  }

  .Typewriter {
    font-size: 1.2rem;
    padding: 0;
    padding-right: 8px;
  }

  .btn-nav {
    left: 220px;
  }

  .btn-nav-push {
    left: 0;
  }

  .ant-menu-item {
    height: 50px !important;
    padding-top: 4px !important;
    padding-left: 25px !important;
    padding-right: 25px !important;
  }

  .menu-footer {
    padding: 20px 25px !important;
  }

  .footer-icons {
    margin-bottom: 10px;
  }

  .footer-icon {
    font-size: 1.8rem;
    opacity: 0.65;
    color: inherit;
  }

  .stack-overflow-icon {
    width: 28px;
  }

  .fun-mode {
    left: 72px;
    bottom: 120px;
  }
}

html,
body {
  height: 100vh !important;
  max-height: 100vh !important;
  min-height: 100vh !important;
  overflow-y: hidden !important;
  min-height: -webkit-fill-available !important;
}

.ant-layout {
  height: 100vh !important;
  max-height: 100vh !important;
  min-height: 100vh !important;
  background: #000 !important;
  min-height: -webkit-fill-available !important;
}

.ant-layout-content {
  font-family: 'Source Sans Pro', sans-serif;
  color: #fff;
  text-align: center;
  font-size: 2rem;
  position: relative;
  top: 0;
  left: 378px !important;
  width: calc(100% - 378px);
  height: 100vh !important;
  max-height: 100vh !important;
  min-height: 100vh !important;
  transition: left 0.8s;
  min-height: -webkit-fill-available !important;
}

.push-body {
  left: 0 !important;
  width: 100%;
}

.content {
  position: relative;
  z-index: 6;
}

h2 {
  color: #fff !important;
}

.tempRocket {
  position: fixed;
  bottom: 0;
  right: -15px;
  width: 50px;
  margin: 0 auto;
  transform: rotate(180deg);
  opacity: 0;
  animation: 5s ease-out 0s 1 flyIn;
  z-index: 4;
}

.rocket {
  position: fixed;
  bottom: 0;
  right: -15px;
  width: 50px;
  margin: 0 auto;
  transform: rotate(180deg);
  opacity: 0;
  animation: 0s ease-out 5s 1 persistRocket forwards;
  z-index: 4;
}

.fuselage {
  width: 50px;
}

.nose {
  width: 25px;
  margin: 0 auto;
  border-top: 15px solid #353535;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
}

.head {
  position: relative;
  display: block;
  border-bottom: 25px solid white;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  height: 0;
  width: 25px;
  margin: 0 auto;
}

.neck {
  position: relative;
  width: 45px;
  height: 30px;
  margin: 0 auto;
  background: #f4f4f4;
}

.body {
  position: relative;
  width: 50px;
  height: 120px;
  background: #f4f4f4;
  border-bottom: 3px solid #a81237;
  border-top: 3px solid #a81237;
}

.reactor {
  position: relative;
  border-bottom: 30px solid #4f4f4f;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  height: 0;
  width: 30px;
  margin: 0 auto;
}

.fire {
  position: relative;
  width: 25px;
  height: 70px;
  margin: 0 auto;
  border-bottom-right-radius: 50%;
  border-bottom-left-radius: 50%;
  animation: fire 0.2s infinite;
}

.spark1 {
  position: relative;
  top: 10px;
  left: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  animation: fireBig 0.5s 0s 10;
}

.spark2 {
  position: relative;
  top: 15px;
  right: 5px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  animation: fireBig 0.3s 0s 17;
}

.spark3 {
  position: relative;
  top: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  animation: fireSmall 0.4s 0s 13;
}

.spark4 {
  position: relative;
  top: 5px;
  left: -20px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  animation: fireSmall 0.7s 0s 8;
}

.spark5 {
  position: relative;
  top: 10px;
  right: -10px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  animation: fireSmall 0.6s 0s 9;
}

.spark6 {
  position: relative;
  top: 20px;
  left: 5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  animation: fireSmall 0.2s 0s 25;
}

.left-fin {
  position: absolute;
  top: 73px;
  right: 50px;
  width: 5px;
  border-bottom: 110px solid white;
  border-left: 60px solid transparent;
}

.right-fin {
  position: absolute;
  top: 73px;
  left: 50px;
  width: 5px;
  border-bottom: 110px solid white;
  border-right: 60px solid transparent;
}

.rotated {
  transform: rotate(0deg);
}

@keyframes fire {
  0% {
    background: linear-gradient(
      to bottom,
      rgba(255, 134, 28, 1) 0%,
      rgba(239, 1, 124, 1) 50%,
      rgba(237, 3, 3, 1) 100%
    );
  }
  50% {
    background: linear-gradient(
      to bottom,
      rgba(237, 3, 3, 1) 0%,
      rgba(255, 134, 28, 1) 51%,
      rgba(239, 1, 124, 1) 100%
    );
  }
  100% {
    background: linear-gradient(
      to bottom,
      rgba(239, 1, 124, 1) 0%,
      rgba(237, 3, 3, 1) 51%,
      rgba(255, 134, 28, 1) 100%
    );
  }
}

@keyframes fireBig {
  0% {
    width: 10px;
    height: 10px;
    background: #ff861c;
  }
  50% {
    background: #ef017c;
  }
  100% {
    width: 60px;
    height: 60px;
  }
}

@keyframes fireSmall {
  0% {
    width: 10px;
    height: 10px;
    background: #ff861c;
  }
  50% {
    background: #ef017c;
  }
  100% {
    width: 40px;
    height: 40px;
  }
}

@keyframes flyIn {
  0% {
    opacity: 1;
    transform: translateX(-100vw) translateY(-100vh) rotate(90deg);
  }
  100% {
    opacity: 1;
    transform: translateX(0) translateY(0) rotate(180deg);
  }
}

@keyframes persistRocket {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes animateStars {
  from {
    background-position: 5% 5%;
  }
  to {
    background-position: 1300% 600%;
  }
}

@media (max-width: 767px) {
  .ant-layout-content {
    left: 220px !important;
    width: 100%;
    padding-top: 2rem;
  }

  .push-body {
    left: 0 !important;
    width: 100%;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .ant-layout-content {
    width: 100%;
  }

  .push-body {
    left: 0 !important;
    width: 100%;
  }
}

